body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  text-align: center;
}

.auth-container, .dashboard-container {
  margin: 40px auto;
  padding: 20px;
  max-width: 400px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
}

input {
  display: block;
  margin: 10px auto;
  padding: 8px;
  width: 90%;
  box-sizing: border-box;
}

button {
  padding: 10px 20px;
  margin: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 16px;
}

button.selected {
  background-color: #3e8e41;
}

.error-message {
  color: red;
  font-size: 12px;
}

.user-type-switch button {
  margin: 5px;
}
